.profile__title {
    margin: 0;
    font-size: 42px;
    line-height: 48px;
    font-weight: 500;
    text-rendering: optimizeLegibility;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 294px; 
}

@media screen and (max-width: 767px) {
    .profile__title {
        text-align: center;
        font-size: 27px;
        line-height: 33px;   
    }
}